@import "common";
@xiaotuW: 80px;
@xiaotuH: 80px;
.content{position: relative;}
.news_top {width:1240px;margin:0 auto;
}
.news_top .title{
  height:27px;
  margin-top: 10px;
  margin-left:0px;
}

.icon_title_bg{
  top:2px;right:2px;width:724px;height:28px;background-position:-12px -330px;display:inline-block;
}
.title span{
  height: 27px;
  line-height: 27px;
  text-align: left;
  left:25px;
  font-size: 16px;
  /*color:#2c2f34;*/
  font-style:italic;
}
.content {width:1240px;height:100%;margin:0 auto;}
.content .content_frame{
  background:#fff;
  width:1240px;
  padding-left: 100px;
  padding-right: 100px;
  padding-bottom:30px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.article_title {
  font-size: 24px;
  color: #2c2f34;
  padding: 20px 0;
  word-break: break-all;
}

.news_info .news_time{font-size: 16px;
  color: #61676f;}
.news_info .introduction{
  font-size: 17px;
  color:#605395;
  text-indent: 3px;
  display: inline;
  word-break: break-all;
  /*border-left:4px solid #abab9b;*/
}
.news_info .left{
  font-size: 17px;
  border-left:4px solid #605395;
  display: inline-block;
  height:18px;
  line-height: 15px;
}
.news_line{
  width:100%;
  margin:0 auto;
  text-align: center;
}
.icon_news_line{
  width:502px;
  height:30px;
  background-position:-184px -798px;display:inline-block;
  margin:20px 0;
}
.news_detail{
  text-align: center;
}
.news_detail p{
  text-align: left;
  font-size: 15px;
  color:#2c2f34;
}

