.w-width {
  height: 80px;
  width: 600px;
  margin-left: 150px;
  background: #dbdbdb;
  .minPic {
    width: 520px;
    height: 81px;
    position: relative;
    overflow: hidden;
  }
  .gallery_demo_unstyled {
    width: 100000px;
    position: absolute;
    left: 0;
    li {
      float: left;
      //display: table-cell;
      vertical-align: middle;
      text-align: center;
      width: @xiaotuW;
      height: @xiaotuH;
      line-height: 77px;
      cursor: pointer;

      margin-right: 7px;
      span {
        width: 0;
        height: 100%;
        display: inline-block;
        vertical-align: middle;
      }
      img {
        display: inline-block;
        text-align: center;
        vertical-align: middle;
        max-width: @xiaotuW;
        max-height:@xiaotuH;
        //border: 1px solid #f1f4f6;
      }
      &.actives {
        background: #2d9ae5;
        img {
          //border: 1px solid #2d9ae5;
        }
      }
    }
  }

}
